<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="clearBusiss">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="创建日期" size="mini">
          <el-date-picker v-model="timeValue" type="daterange" @change="changeDate" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="所属区域" size="mini" class="wauto">
          <getAreaList v-if="defaultArea2 && defaultArea2.length" @focus="getprovinceFocus" @changeDefaultArea="changeDefaultArea" :defaultArea="defaultArea2" />
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <!-- <el-input v-model="params.customer_name" placeholder="请输入"></el-input> -->
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok" @change="getCustomerChange">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addFun" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
      <!-- <el-button plain icon="iconfont icon-daochu10" @click="exportFile" size="mini" v-if="authority.b_export==1">导出</el-button> -->
      <div class="add_delet" v-if="authority.b_export==1">
        <el-dropdown placement="bottom-start">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" :row-class-name="tableRowClassName" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @selection-change="handleSelectionChange" max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44"></el-table-column>
        <el-table-column label="操作" fixed="right" width="100">
          <template slot-scope="scope">
            <el-button @click="examine(scope, scope.row)" type="text" size="small" v-if="authority.b_info==1">
              查看
            </el-button>
            <el-button @click="handleEdit(scope, scope.row)" type="text" size="small" v-if="authority.b_edit==1">
              修改
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="销售组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" width="120" label="客户名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="consignee_name" width="180" label="收货人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="consignee_mobile" width="180" label="联系电话"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="province_name" width="180" label="区域">
          <template slot-scope="scope">
            <span>{{scope.row.province_name}}</span>
            <span>{{scope.row.city_name}}</span>
            <span>{{scope.row.area_name}}</span>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="address" width="180" label="详细收货地址"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="is_default" width="180" label="是否默认地址"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="180" label="创建人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_time" width="180" label="创建时间"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_user_name" width="180" label="修改人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_time" width="180" label="修改时间"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" width="180" label="备注"></el-table-column>

      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <!-- @close="closeDialog('ruleForm')" -->
    <div v-if="dialogFormVisible">
      <el-dialog :close-on-click-modal="false" :title="title" @open="open" center :visible.sync="dialogFormVisible" width="480px" class="customaddressDialog">
        <el-form :model="ruleForm" ref="ruleForm" class="errorItem errorItemMl121">
          <el-form-item label="销售组织" prop="company_id" :rules="[{ required: true, message: '请选择销售组织', trigger: 'change' }]">
            {{(function(){if(ruleForm.company_id == 0)ruleForm.company_id = null})()}}
            <el-select clearable v-model="ruleForm.company_id" @focus="companyListInvok" placeholder="请选择" :disabled="disabled" @change="ruleFormCompanyChnage">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="客户名称" prop="customer_id" :rules="[{ required: true, message: '请选择客户名称', trigger: 'change' }]">
            <el-select v-model="ruleForm.customer_id" filterable placeholder="请输入" :disabled="disabled" clearable @focus="dialogCustomerList" :loading="ruleFormLoading" remote :remote-method="dialogCustomerList">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in ruleFormCustomerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="收货人" prop="consignee_name" :rules="[{ required: true, message: '请输入收货人', trigger: 'blur' }]">
            <el-input v-model="ruleForm.consignee_name" :disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item label="联系电话" prop="consignee_mobile" :rules="rules.phone">
            <el-input v-model="ruleForm.consignee_mobile" :disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item label="所属区域" size="mini" prop="cityList" class="wauto wauto_area" :rules="rules.areaadress">
            <getAreaList v-if="defaultArea && defaultArea.length && is_checked_search" @focus="getprovinceAddFocus" @changeDefaultArea="changeDefaultAreas" :defaultArea="defaultArea" />
            <span v-else>
              {{ruleForm.province_name}} {{ruleForm.city_name}} {{ruleForm.area_name}}
            </span>
          </el-form-item>
          <el-form-item label="详细收货地址" prop="address" :rules="[{ required: true, message: '请输入收货地址', trigger: 'blur' }]">
            <span v-if="is_checked_search">
              <el-input size="small" type="text" v-model="ruleForm.address" suffix-icon="el-icon-edit-outline" :maxlength="300" :disabled="disabled" style="width: 250px;"></el-input>
            </span>
            <span v-else>
              <el-input size="small" type="text" v-model="ruleForm.address" suffix-icon="el-icon-edit-outline" :maxlength="300" :disabled="disabled" style="width: 250px;"></el-input>
            </span>
          </el-form-item>
          <el-form-item label="是否默认地址" class="el-form-item_shortInput">
            <el-radio-group v-model="ruleForm.is_default" v-if="is_checked_search">
              <el-radio label='1'>是 </el-radio>
              <el-radio label='0'>否</el-radio>
            </el-radio-group>
            <span v-else>
              <i v-if="ruleForm.is_default==1">是</i>
              <i v-if="ruleForm.is_default==0">否</i>
            </span>
          </el-form-item>
          <el-form-item label="备注">
            <el-input size="small" type="text" v-model="ruleForm.remark" suffix-icon="el-icon-edit-outline" :maxlength="300" :disabled="disabled" style="width: 250px;"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeDialog('ruleForm')" v-if="is_checked_search">取 消</el-button>
          <el-button @click="closeDialog('ruleForm')" v-else>返 回</el-button>
          <el-button type="primary" @click="dialogFormsubmit('ruleForm')" v-if="is_checked_search">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  getData,
  exportFile,
  opthionList
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
import getAreaList from "common/components/public/getAreaList";
export default {
  name: "customeCustomaddress",
  data() {
    return {
      params: {
        export_type: 1
      },
      defaultArea: [0], //默认的区域id
      defaultArea2: [0],
      province: {
        options: []
      },
      loading:false,
      city: {
        options: []
      },
      area: {
        options: []
      },
      provinceadd: {
        options: []
      },
      cityadd: {
        options: []
      },
      areaadd: {
        options: []
      },
      ruleForm: {
        is_default: "1",
        cityList: ""
      },
      ruleFormLoading: false, // dialog 弹框中的 客户列表加载项
      ruleFormCustomerList: [], // dialog 弹框中的 客户列表
      dialogFormVisible: false,
      is_checked_search: false,
      title: "新增/修改客户收货信息",
      disabled: false,
      cityList: "",
      status: "add"
    };
  },
  components: {
    getAreaList //区域列表
  },
  methods: {
    ...mapActions(["customaddressIndexACT"]),
    tableRowClassName({ row, rowIndex }) {
      if (row.canNotDelete) {
        return "error-row";
      }
      return "";
    },
    // 处理新增修改 弹框中的客户
    dialogCustomerList(romteVal) {
      this.ruleFormLoading = true;
      let obj = {
        company_id: this.ruleForm.company_id
      };
      if (typeof romteVal == "string") {
        obj.customer_name = romteVal;
      }
      this.$axMethod
        .getCustomerListOpen(obj)
        .then(res => {
          this.ruleFormLoading = false;
          if (res.data.code == 200) {
            this.ruleFormCustomerList = res.data.data;
          }
        })
        .catch(error => {
          this.ruleFormLoading = false;
        });
    },
    // 弹框中 销售组织切换
    ruleFormCompanyChnage() {
      this.$set(this.ruleForm, "customer_id", ""); // 初始化客户名称下拉选中项
    },
    clearBusiss(val) {
      this.$set(this.searchParams, "business_id", null);
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中项
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
    },
    getCustomerChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    getprovinceAddFocus() {
      this.$common.options("getAreaListOption", this.provinceadd, {
        level: 2
      });
    },
    getprovinceFocus() {
      this.$common.options("getAreaListOption", this.province, {
        level: 2
      });
    },
    closeDialog(ruleForm) {
      this.disabled = false;
      this.dialogFormVisible = false;
      Object.keys(this.ruleForm).forEach(key => (this.ruleForm[key] = ""));
      this.$refs[ruleForm].resetFields();
      this.ruleForm.is_default = "1";
    },
    changeDate(val) {
      this.params.start_time = val[0];
      this.params.end_time = val[0];
    },
    changeProvince() {
      this.params.city_id = "";
      this.$common.options("getAreaListOption", this.city, {
        level: 3,
        is_page: 1,
        p_id: this.params.province_id
      });
    },
    changeDefaultArea(addressId) {
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    changeDefaultAreas(addressId) {
      this.$set(this.ruleForm, "cityList", addressId.join(","));
      this.ruleForm.province_id = addressId[0];
      this.ruleForm.city_id = addressId[1];
      this.ruleForm.area_id = addressId[2];
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      this.customaddressIndexACT({
        // commont: "customerinfoAdd1",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      getData(this.searchParams);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.defaultArea2 = [0];
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: this.params, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getAddressList", //接口名
        vm: this //传递的this
      });
      this.onSearch();
    },
    exportOrder(scheduleType) {
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].address_id);
      }
      var rebateArr = addr.toString();

      let arg = {
        params: {
          ...this.params,
          address_id: rebateArr,
          export_type: scheduleType
        },
        API: "exportAddressListUrl"
      };

      exportFile(arg);
    },
    exportFile() {
      //导出

      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].address_id);
      }
      var rebateArr = addr.toString();

      let arg = {
        params: {
          ...this.params,
          address_id: rebateArr
        },
        API: "exportAddressListUrl"
      };

      exportFile(arg);
    },
    addFun() {
      this.status = "add";
      this.title = "新增客户收货信息";
      this.is_checked_search = true;
      this.dialogFormVisible = true;
      this.disabled = false;
      this.defaultArea = [0];
      Object.keys(this.ruleForm).forEach(key => (this.ruleForm[key] = ""));
      this.$set(this.ruleForm, "cityList", "");
      // 修改弹框数据初始化
      this.companyList.push({
        company_id: this.params.company_id,
        company_name: this.params.company_name
      });
    },
    deletes() {
      var deledata = {
        deleID: "address_id",
        deleSelect: this.multipleSelection,
        deleApi: "delAdress",
        list: {
          UrlApi: "getAddressList",
          data: this.TableData
        }
      };
      this.$common.deletes(deledata, "删除", this);
      // this.$common.BatchDeletion(deledata, "删除", this);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    open() {
      this.defaultArea = [0];
    },
    dialogFormsubmit(ruleForm) {
      var vm = this;
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          if (this.status == "add") {
            let funcan = {
              eachData: this.ruleForm,
              UrlApi: "addAddress",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getAddressList",
                data: this.TableData,
                esData: this.searchParams.params
              }
            };
            this.$common.addAndupdateFun(funcan);
            // this.defaultArea2 = [0];
          } else {
            let funcan = {
              eachData: this.ruleForm,
              UrlApi: "editAddress",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getAddressList",
                data: this.TableData,
                esData: this.searchParams.params
              }
            };
            this.$common.addAndupdateFun(funcan);
            // this.defaultArea2 = [0];
          }
        }
      });
    },
    examine(scope, row) {
      this.dialogFormVisible = true;
      this.$axios({
        method: "get",
        url: this.$api.getAddressInfo,
        params: {
          address_id: row.address_id
        }
      }).then(res => {
        this.ruleForm = res.data.data;
        this.ruleForm.is_default = "" + res.data.data.is_default + "";
        this.ruleForm.customer_id = res.data.data.customer_name;
        this.is_checked_search = false;
        this.title = "查看客户收货信息";
        this.disabled = true;
        this.defaultArea = [
          res.data.data.province_id,
          res.data.data.city_id,
          res.data.data.area_id
        ];
        this.companyList.push({
          company_name: this.ruleForm.company_name,
          company_id: this.ruleForm.company_id
        });
        this.$set(this.ruleForm, "cityList", this.defaultArea.join(","));
        this.ruleForm.province_id = this.defaultArea[0];
        this.ruleForm.city_id = this.defaultArea[1];
        this.ruleForm.area_id = this.defaultArea[2];
      });
      // 修改弹框数据初始化
      this.companyList.push({
        company_id: this.params.company_id,
        company_name: this.params.company_name
      });
    },
    handleEdit(scope, row) {
      this.status = "update";
      this.title = "修改客户收货信息";
      this.dialogFormVisible = true;
      this.disabled = false;
      // this.clienteleListInvok(); // 因不用同一个数组故不使用该方法请求客户列表
      this.$axios({
        method: "get",
        url: "/custom/Address/getAddressInfo",
        params: {
          address_id: row.address_id
        }
      }).then(res => {
        this.ruleForm = res.data.data;
        this.ruleForm.is_default = "" + res.data.data.is_default + "";
        this.is_checked_search = true;
        // 公司
        this.companyList.push({
          company_name: this.ruleForm.company_name,
          company_id: this.ruleForm.company_id
        });
        // 客户
        this.ruleFormCustomerList = [
          {
            customer_id: res.data.data.customer_id,
            customer_name: res.data.data.customer_name
          }
        ];
        // 所属区域
        this.defaultArea = [
          res.data.data.province_id,
          res.data.data.city_id,
          res.data.data.area_id
        ];
        this.$set(this.ruleForm, "cityList", this.defaultArea.join(","));
        this.ruleForm.province_id = this.defaultArea[0];
        this.ruleForm.city_id = this.defaultArea[1];
        this.ruleForm.area_id = this.defaultArea[2];
      });
      // 修改弹框数据初始化
      this.companyList.push({
        company_id: this.params.company_id,
        company_name: this.params.company_name
      });
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    }
  },
  computed: {
    ...mapGetters(["customeraddressDefault_showGet"])
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    if (
      this.customeraddressDefault_showGet &&
      this.customeraddressDefault_showGet.params
    ) {
      let objMessage = this.customeraddressDefault_showGet.params;
      this.defaultArea2 = [
        objMessage.province_id,
        objMessage.city_id,
        objMessage.area_id
      ];
      if (this.customeraddressDefault_showGet.params.start_time) {
        const {
          start_time,
          end_time
        } = this.customeraddressDefault_showGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      this.$set(this, "params", this.customeraddressDefault_showGet.params);
      opthionList(this, this.customeraddressDefault_showGet.params);
    }
    if (
      this.customeraddressDefault_showGet &&
      this.customeraddressDefault_showGet.goPage
    ) {
      this.params.page = this.customeraddressDefault_showGet.goPage;
      this.params.per_page = this.customeraddressDefault_showGet.pageSize;
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getAddressList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

